<template>
  <div class="songlistetails">
    <div>
      <van-nav-bar left-text="返回" fixed @click-left="goback" left-arrow />
    </div>
    <div class="songlist-top clearfix"  :style="{'background-image': 'url('+backgroundUrl+')'}">
      <div class="songlist-img-box fl">
        <img class="auto-img" :src="coverImgUrl" />
      </div>
      <div class="songlist-title-box fl">
        <div class="songlist-title">
          {{songlistname}}
        </div>
        <div class="title-img-box">
          <img
            class="auto-img titleimg"
            :src="songlistavatarUrl"
          />
          <div class="title-img-box-name">{{songlistnickname}}</div>
        </div>
        <div class="title-text">
          {{ songlistdescription}}
        </div>
      </div>
    </div>
    <div class="song-list-box">
      <div class="song-list-top">
        <div class="song-list-left">
          <div class="song-list-img-box">
            <img class="auto-img" @click="playAllMusic" src="../assets/images/mv-play.png"/>
          </div>
          <div class="song-list-playall">播放全部<span>(共{{songListsData.length}}首)</span></div>
        </div>
        <div class="song-list-collect" >收藏</div>
      </div>
      <div class="song-list-top" v-for="(item,index) in songListsData" :key="index" @click="goPlayPage(item)">
        <div class="song-list-left">
          <div class="song-list-img-box">{{index+1}}</div>
          <div class="song-list-playall">
            <div class="song-list-content van-ellipsis">{{item.name}}</div>
            <div class="song-list-detail van-ellipsis">{{item.ar[0].name}} - {{item.al.name}}</div>
          </div>
        </div>
        <div @click.stop="playmusic(item)" class="playbtn"><van-icon name="play-circle-o" /></div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/SongListDetails.less";

// import {setupCookie} from "../assets/js/setupCookie"
export default {
  data() {
    return {
      songListData: [],
      // songListDetailsData: [],
      songListsData:[],
      songlistid:0,
      type:0,
      //歌单信息
      backgroundUrl:'',
      coverImgUrl:'',
      songlistname:'',
      songlistavatarUrl:'',
      songlistnickname:'',
      songlistdescription:'',
      //
    };
  },
  created() {
    
    this.songlistid = this.$route.query.id

    this.getsongListDetailsData(this.songlistid);

  },
   beforeRouteEnter(to, from, next) {
    if (to.name !== from.name) {
      next( vm => {
        vm.songlistid = to.query.id
      })
    }
  },
  computed: {
  },
  watch: {
    songlistid(val,oval){
      if (val != oval) {
        this.songlistid = val
        this.getsongListDetailsData(val);
      }
    },


  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
     //获取歌单详情
    getsongListDetailsData(musicid) {
      this.axios({
        method: "GET",
        url: "/playlist/detail",
        params: {
          id: musicid,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {

            this.backgroundUrl = result.data.playlist.backgroundCoverUrl
            this.coverImgUrl = result.data.playlist.coverImgUrl
            this.songlistname = result.data.playlist.name
            this.songlistavatarUrl = result.data.playlist.creator.avatarUrl
            this.songlistnickname = result.data.playlist.creator.nickname
            this.songlistdescription = result.data.playlist.description
            
            this.songListData = result.data.playlist.trackIds.map( v => {
              return v.id
            });
          }
          this.getmusicData();
        })
        .catch((err) => {
          console.error(err)
        });
    },
    // 获取歌曲详情
    getmusicData() {
      // // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: `/song/detail?ids=${this.songListData}`,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.songListsData = result.data.songs
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },

    // 播放当前歌单
    playAllMusic(){
      this.$store.commit("setplaymusicids",this.songListData)
      let musicData = {
        id:this.songListData[0],
      }
      this.$store.commit("setmusicid",musicData)
    },

    //播放不跳转
    playmusic(item){
      this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
    },
     //前往详情页面
    goPlayPage(item){
      this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
      }
  }
};
</script>